import { AnyAction, ThunkDispatch } from '@reduxjs/toolkit';

import type OpenFin from '@openfin/core';

import { convertLayoutSnapshotToPages, PagesLayoutSnapshot } from '@common/api/pages/helper';

import {
    AttachedPagesRootState,
    selectActivePageWithIndex,
    selectPageByLayoutContainerKey,
    setPages
} from '@enterprise/slices/pages/attached';

import { detachPagesFromWindow, setActivePageForWindow } from './attached';

export const makeLayoutManagerOverride =
    (storeDispatch: ThunkDispatch<unknown, unknown, AnyAction>, storeGetState: () => AttachedPagesRootState) =>
    (Base: OpenFin.LayoutManagerConstructor<OpenFin.LayoutSnapshot>) =>
        class LayoutManagerOverride extends Base implements OpenFin.LayoutManager<OpenFin.LayoutSnapshot> {
            async applyLayoutSnapshot(snapshot: PagesLayoutSnapshot): Promise<void> {
                storeDispatch(setPages(convertLayoutSnapshotToPages(snapshot)));
            }
            async showLayout({ layoutName }: OpenFin.LayoutIdentity): Promise<void> {
                const page = selectPageByLayoutContainerKey(storeGetState(), layoutName);
                if (!page?.pageId) {
                    console.debug(
                        `layoutManager::showLayout - calling selectPageByLayoutContainerKey() returned an undefined page`,
                        layoutName
                    );
                }
                setActivePageForWindow({ identity: fin.me.identity, pageId: page.pageId });
            }
            resolveLayoutIdentity(identity?: OpenFin.Identity | OpenFin.LayoutIdentity): OpenFin.LayoutIdentity {
                if (!identity) {
                    const [currentActivePage] = selectActivePageWithIndex(storeGetState());
                    if (!currentActivePage?.pageId) {
                        console.debug(
                            `layoutManager::resolveLayoutIdentity - calling selectActivePageWithIndex() returned an undefined page`,
                            identity
                        );
                    }
                    return { ...fin.me.identity, layoutName: currentActivePage.layoutContainerKey };
                }
                return super.resolveLayoutIdentity(identity);
            }

            async removeLayout({ layoutName }: OpenFin.LayoutIdentity): Promise<void> {
                const page = selectPageByLayoutContainerKey(storeGetState(), layoutName);
                if (!page?.pageId) {
                    console.debug(
                        `layoutManager::removeLayout - calling selectPageByLayoutContainerKey() returned an undefined page`,
                        layoutName
                    );
                }
                await detachPagesFromWindow({ identity: fin.me.identity, pageIds: [page.pageId] });
            }
        };
