import type OpenFin from '@openfin/core';

import { isLandingPage } from '@common/utils/enterpriseBrowser';
import { addInteropListener, InteropEventType } from '@common/utils/interop';
import {
    addLayoutEventListener,
    findPageForView,
    LayoutDOMEvent,
    layoutNameInitializedMap
} from '@common/utils/layout';
import { currentOFIdentity, getCurrentOFWindow } from '@common/utils/window';

import { requestPageChangesDecision } from '@common/api/pages/internal';

import {
    selectIsActivePageChanging,
    selectIsDetachingPages,
    selectPage,
    selectPageByLayoutContainerKey,
    selectPageWithIndex,
    updatePageAtIndex
} from '@enterprise/slices/pages/attached';

import { getViewTabHat } from '@enterprise/components/LayoutContainer/utils';

import { PageChangeEventDetails, PageChangeEventType } from '@client-platform/shapes';

import { dispatch, getState } from './state';

/**
 * Serves purpose of tracking current view url in view-title-updated event fires without url changes
 * we will prevent unnecessary dirtying of pages. This is a workaround for the lack of a view-url-updated event.
 * This is a historical decision and should be revisited.
 */

const viewIdAndUrlMap: Map<string, string> = new Map();

/**
 * Handle dirty events for the target window.
 *
 * The dirty event will be ignored if:
 *    - The event came from another app. (This should never happen, but better safe then sorry.)
 *    - The window is in the middle of switching active pages.
 *
 * @param layoutContainerKey they key of the layout.
 * @param eventDetails
 */
const handleDirtyEvent = async (eventDetails?: PageChangeEventDetails, layoutContainerKey?: string) => {
    let isDetachingPages = selectIsDetachingPages(getState());
    let isActivePageChanging = selectIsActivePageChanging(getState());

    console.debug(
        'handleDirtyEvent',
        'isDetachingPages',
        isDetachingPages,
        'isActivePageChanging',
        isActivePageChanging
    );

    const page = selectPageByLayoutContainerKey(getState(), layoutContainerKey);

    if (isDetachingPages || isActivePageChanging || !page.isLayoutReady) {
        return;
    }

    const [_, index] = selectPageWithIndex(getState(), page.pageId);

    // page will have an outdated layout, so we update the layout property to the current layout.
    const currentLayout = await fin.Platform.Layout.wrapSync({
        ...fin.me.identity,
        layoutName: layoutContainerKey
    }).getConfig();
    const { hasUnsavedChanges } = await requestPageChangesDecision({
        page: { ...page, layout: currentLayout },
        identity: currentOFIdentity,
        eventDetails
    });

    // Update the values from state in case they changed during async calls
    isDetachingPages = selectIsDetachingPages(getState());
    isActivePageChanging = selectIsActivePageChanging(getState());

    if (isDetachingPages || isActivePageChanging) {
        console.debug(`Not marking page with id ${page.pageId} as dirty as active page changed`);
        return;
    }

    dispatch(
        updatePageAtIndex({
            index,
            page: { hasUnsavedChanges }
        })
    );
};

/**
 * Listen for layout DOM events to determine if the state of the active page is now dirty.
 *
 * The layout DOM event will be ignored if:
 *    - The layout has not yet finished initializing.
 */
const tabEventHandler = async ({
    isInitialized,
    detail,
    type
}: Extract<LayoutDOMEvent, { type: 'tab-created' | 'tab-closed' }>) => {
    console.debug('handling page dirty event caused by layout DOM event');
    if (type === 'tab-created' && !isInitialized) return;

    const layoutContainerKey = detail.originalElement.id;
    if (type === 'tab-closed' && !isInitialized) {
        // if user is able to close a tab, everything is already set up, don't need to wait for layout-initialized
        layoutNameInitializedMap.set(layoutContainerKey, true);
    }

    await handleDirtyEvent(
        {
            type: type == 'tab-created' ? PageChangeEventType.ViewAdded : PageChangeEventType.ViewRemoved,
            viewIdentity: { name: detail.name, uuid: detail.uuid }
        },
        layoutContainerKey
    );
};

/** Get the layout container key for a view. */
const getLayoutContainerKey = async (viewIdentity: OpenFin.Identity): Promise<string | undefined> => {
    const view = fin.View.wrapSync(viewIdentity);
    const win = await view.getCurrentWindow();
    if (win.identity.name !== currentOFIdentity.name) return;
    const page = await findPageForView(viewIdentity.name, currentOFIdentity);
    if (!page) {
        console.warn(`No page found for view ${viewIdentity.name}`);
        return;
    }
    const storedPage = selectPage(getState(), page.pageId);
    return storedPage?.layoutContainerKey;
};
/**
 * only show the view tab icon when not on the landing page
 */
const showHideTabFavIcon = async (url: string, viewName: string) => {
    const isLanding = isLandingPage(url);
    const tab = getViewTabHat(viewName);
    (tab.querySelector('.tab-icon') as HTMLImageElement).style.display = isLanding ? 'none' : 'inherit';
};

/**
 * Check if a view's url has changed.
 * If it has, set the attached window's active page as dirty.
 */
const handleViewUrlChangedEvent = async ({ viewIdentity }: { viewIdentity: OpenFin.Identity }) => {
    console.debug('handling page dirty event caused by view-url-changed event for view with identity:', viewIdentity);
    const view = fin.View.wrapSync(viewIdentity);
    const opts = await view.getInfo();

    if (!viewIdAndUrlMap.get(viewIdentity.name) || viewIdAndUrlMap.get(viewIdentity.name) === opts.url) {
        console.debug("Ignoring view-url-changed updated event as the view hasn't finished loading");
        return;
    } else {
        window.requestAnimationFrame(() => showHideTabFavIcon(opts.url, viewIdentity.name));

        console.debug(
            `View url has changed, marking page as dirty, viewIdAndUrlMap: ${viewIdAndUrlMap.get(
                viewIdentity.name
            )}, opts.url: ${opts.url}`
        );
    }

    viewIdAndUrlMap.set(viewIdentity.name, opts.url);

    await handleDirtyEvent(
        {
            type: PageChangeEventType.UrlChange,
            viewIdentity,
            urlBefore: viewIdAndUrlMap.get(viewIdentity.name),
            urlAfter: opts.url
        },
        await getLayoutContainerKey(viewIdentity)
    );
};

const handleViewFinishedLoadEvent = async ({ viewIdentity }: { viewIdentity: OpenFin.Identity }) => {
    const view = fin.View.wrapSync(viewIdentity);
    const opts = await view.getInfo();

    viewIdAndUrlMap.set(viewIdentity.name, opts.url);
};

/**
 * Register a listener that tracks the dirty state of pages.
 *
 * A page is dirty if its state has been changed by a user at runtime,
 * and said changes have not yet been persisted in the Content Discovery Service.
 *
 * A page is considered dirty if:
 *  - A tab DOM element has been created.
 *  - A tab DOM element has been moved. (Tab DOM element is destroyed and recreated.)
 *  - The container DOM element of a view has been resized.
 *  - Color linking of a view within a page has changed.
 *  - URL of a view within a page has changed.
 */
export const registerDirty = () => {
    addLayoutEventListener('tab-created', tabEventHandler);
    addLayoutEventListener('tab-closed', tabEventHandler);
    addInteropListener(InteropEventType.JoinedContextGroup, async ({ viewIdentity, contextGroupId }) =>
        handleDirtyEvent(
            { viewIdentity, type: PageChangeEventType.PageContextAdded, contextId: contextGroupId },
            await getLayoutContainerKey(viewIdentity)
        )
    );
    addInteropListener(InteropEventType.RemovedFromContextGroup, async ({ viewIdentity }) =>
        handleDirtyEvent(
            { viewIdentity, type: PageChangeEventType.PageContextRemoved },
            await getLayoutContainerKey(viewIdentity)
        )
    );
};

export const registerViewDirtyEvents = async () => {
    const win = getCurrentOFWindow();
    win.addListener('view-did-finish-load', handleViewFinishedLoadEvent);
    win.addListener('view-url-changed', handleViewUrlChangedEvent);
    win.addListener('view-attached', async (e) => {
        // this is a workaround for the lack of a view-url-changed event on mac
        // so view only gets dirty if url changes second time
        try {
            const view = fin.View.wrapSync(e.viewIdentity);
            const opts = await view.getInfo();
            viewIdAndUrlMap.set(e.viewIdentity.name, opts.url);
        } catch (ex: any) {
            console.warn('Error while handling view-attached event in page detection logic', ex);
        }
    });
};
