import { useEffect } from 'react';
import { useStore } from 'react-redux';

import type OpenFin from '@openfin/core';

import { initLayoutListeners } from '@common/utils/layout';

import { getBrowserChannelClient } from '@common/api/protocol/browser';

import { AttachedPagesRootState, setIsInitialized } from '@enterprise/slices/pages/attached';

import { registerBrowserProtocolActions, registerPagesProtocol } from '@enterprise/api/protocol';

import { TabFocusRootState } from '@enterprise/slices/tab-focus';

let channelCreationPromise: Promise<OpenFin.ChannelProvider> | undefined = undefined;

/**
 * Hook for initializing the Browser protocol and Layouts.
 * The Browser protocol includes attached pages protocol as well as other actions specific to the browser instance.
 * The attached page protocol uses redux to store all of its state and to trigger UI updates.
 * Hence, we hook into react context to get the store's `getState` and `dispatch` function for the attached page API.
 */
export const useBrowserProtocol = () => {
    const store = useStore();

    useEffect(() => {
        const initializeBrowser = async () => {
            if (channelCreationPromise) {
                return;
            }
            channelCreationPromise = getBrowserChannelClient();
            // FIXME: this promise is not awaited
            initLayoutListeners();

            channelCreationPromise.then((channel) => {
                registerPagesProtocol(
                    channel,
                    store.dispatch,
                    store.getState as () => AttachedPagesRootState & TabFocusRootState
                )
                    .then(() => {
                        store.dispatch(setIsInitialized(true));
                    })
                    .catch((err) => console.error('useBrowserProtocol: failed to init pages', err));

                registerBrowserProtocolActions(channel);
            });
        };

        initializeBrowser();
    }, [store]);
};
