import { FC } from 'react';
import { useSelector } from 'react-redux';

import type { AppProps } from 'next/app';

import { ColorScheme } from '@openfin/ui-library';

import App from '@common/pages/_app';

import { selectIsWindows } from '@common/slices/environment';
import { selectCurrentScheme } from '@common/slices/target-theming';

import BrowserThemeProvider from '@enterprise/components/App/ThemeProvider/BrowserThemeProvider';

import { BrowserContext } from '@common/hooks/context';
import useSystemSchemeCheck from '@common/hooks/useSystemSchemeCheck';
import { useBrowserProtocol } from '@enterprise/hooks/useBrowserProtocol';

import EnterprisePlatformStyles from './EnterprisePlatformStyles';

const PlatformsStyles = () => {
    const currentScheme = useSelector(selectCurrentScheme);
    const isWindows = useSelector(selectIsWindows);
    const { isSchemeDark } = useSystemSchemeCheck();

    /* Ask product weather we need to combine styles and take improvments from Enterprise.... */
    return (
        <EnterprisePlatformStyles
            scheme={currentScheme === ColorScheme.dark ? ColorScheme.dark : ColorScheme.light}
            isWindows={isWindows}
            isSchemeDark={isSchemeDark}
        />
    );
};

/**
 * Entry point for a page that is in a browser window and requires standard Browser dependencies.
 */
const BrowserWindowApp: FC<AppProps> = (props) => {
    useBrowserProtocol();

    return (
        <BrowserContext>
            <BrowserThemeProvider>
                <PlatformsStyles />
                <App {...props} />
            </BrowserThemeProvider>
        </BrowserContext>
    );
};

export default BrowserWindowApp;
