import { PageLayoutsWithSelectedViews } from '@common/api/pages/shapes';

import { AttachedPagesRootState } from '@enterprise/slices/pages/attached';

import { TabFocusRootState } from '@enterprise/slices/tab-focus';

export const getLayoutsWithSelectedViewsInternal = (
    storeGetState: () => AttachedPagesRootState & TabFocusRootState
): PageLayoutsWithSelectedViews[] => {
    const store = storeGetState();
    const { attachedPages } = store.attachedPages;
    const { layoutsWithSelectedViews } = store.tabFocus;
    const activePage = attachedPages.find((e) => e.isActive);
    return attachedPages.map((page) => ({
        pageId: page.pageId,
        layoutContainerKey: page.layoutContainerKey,
        selectedViews: layoutsWithSelectedViews[page.layoutContainerKey],
        isActive: activePage?.pageId === page.pageId
    }));
};
