import { css, DefaultTheme } from 'styled-components';

import debounce from 'lodash.debounce';

const applyCondensedTabClassNames = (tabGroup: Element) => {
    const tabs = tabGroup.querySelectorAll('.lm_tab');
    let condensedTabClassName = '';
    const { width } = tabs[0].getBoundingClientRect();

    if (width === 24) {
        condensedTabClassName = 'view-tab-condensed-sm';
    } else if (width < 44) {
        condensedTabClassName = 'view-tab-condensed-md';
    } else if (width <= 56) {
        condensedTabClassName = 'view-tab-condensed-lg';
    }

    tabs.forEach((tab) => {
        tab.classList.forEach((className) => className.includes('view-tab-') && tab.classList.remove(className));
        if (condensedTabClassName) {
            tab.classList.add(condensedTabClassName);
        }
    });
};

// Enables horizontal scroll on tabs
const handleTabContainerScroll = (event: WheelEvent) => {
    const { deltaX, deltaY } = event;
    const tabHeader = (event.target as HTMLElement).closest('.lm_tabs');
    if (Math.abs(deltaY) > Math.abs(deltaX)) {
        tabHeader.scrollLeft += deltaY - deltaX;
    }
};

// Scrolls tab container when mouse nears edge of tab header
const handleAutoScrollOnTabDrag = (event: MouseEvent) => {
    const tabHeader = (event.target as Element).closest('.lm_header');
    const tabGroup = tabHeader.querySelector('.lm_tabs') as HTMLDivElement;
    const isDraggingTab = !!tabGroup.querySelector('.lm_drop_tab_placeholder');

    if (isDraggingTab) {
        const tabGroupRect = tabGroup.getBoundingClientRect();
        if (event.clientX < tabGroupRect.left + 12) {
            tabGroup.scrollBy({ left: -1 });
        }
        if (event.clientX > tabGroupRect.right - 12) {
            tabGroup.scrollBy({ left: 1 });
        }
    }
};

const toggleScrollShadows = (containerRef: HTMLDivElement) => {
    const tabHeaders = containerRef.querySelectorAll('.lm_header');
    tabHeaders.forEach((tabHeader: HTMLDivElement) => {
        const tabGroup = tabHeader.querySelector('.lm_tabs');
        const scrollShadows = tabGroup.querySelectorAll('.scroll-shadow');
        const newTabButton: HTMLDivElement = tabHeader.querySelector('.newTabButton');
        const controlsContainer: HTMLDivElement = tabHeader.querySelector('.lm_controls');

        // Create space for of absolute-positioned controls container (e.g. popout icon)
        tabHeader.style.paddingRight = `${controlsContainer?.offsetWidth}px`;

        // tabs.length > 1 prevents scroll shadows when moving tab from
        // tab header that only has ONE tab
        const tabs = tabGroup.querySelectorAll('.lm_tab');
        if (tabGroup.scrollWidth > tabGroup.clientWidth && tabs.length > 1) {
            // Move new tab button from tab list to tab header so
            // 1) Plus button doesn't move since it's not part of the tab list
            // 2) New tabs can properly scroll into view
            // Golden layouts will automatically re-add a plus button into the tab list
            // but we will hide it using CSS in the presence of this button.
            if (newTabButton?.parentElement.classList.contains('lm_tabs')) {
                tabHeader.insertBefore(newTabButton, tabGroup);
            }

            let tabGroupRightScrollShadow = Array.from(scrollShadows).find((element) =>
                element.classList.contains('right-scroll-shadow')
            ) as HTMLDivElement;
            // Apply scroll shadows
            if (!scrollShadows.length) {
                const tabGroupLeftScrollShadow = document.createElement('div');
                tabGroupLeftScrollShadow.classList.add('scroll-shadow', 'left-scroll-shadow');
                tabGroupRightScrollShadow = document.createElement('div');
                tabGroupRightScrollShadow.classList.add('scroll-shadow', 'right-scroll-shadow');

                tabGroup.appendChild(tabGroupLeftScrollShadow);
                tabGroup.appendChild(tabGroupRightScrollShadow);

                tabGroup.addEventListener('wheel', handleTabContainerScroll);
                tabHeader.addEventListener('drag', handleAutoScrollOnTabDrag);
            }

            // Offset absolute positioned newTab button and scroll shadow if there is a visible controls container (e.g. popout button)
            if (newTabButton) {
                newTabButton.style.right = `${controlsContainer.offsetWidth}px`;
            }
            tabGroupRightScrollShadow.style.right = `${
                (controlsContainer?.offsetWidth || 0) + (newTabButton ? 28 : 0)
            }px`;
        } else if (tabGroup.scrollWidth <= tabGroup.clientWidth) {
            // Remove the tab header plus button we inserted for the scrolling case
            if (newTabButton?.parentElement.classList.contains('lm_header')) {
                newTabButton.remove();
            }

            if (scrollShadows.length) {
                scrollShadows[0].remove();
                scrollShadows[1].remove();
            }
        }

        applyCondensedTabClassNames(tabGroup);
    });
};

export const debouncedToggleScrollShadows = debounce(toggleScrollShadows, 100);

export const getViewTabTitleOverflowShadow = (
    theme: DefaultTheme,
    isSchemeDark: boolean,
    isTabHoveredOrActive: boolean,
    isCloseButtonShowing: boolean
) => {
    const colorToken = isSchemeDark
        ? isTabHoveredOrActive
            ? 'background5'
            : 'background3'
        : isTabHoveredOrActive
        ? 'background3'
        : 'background1';

    if (isCloseButtonShowing) {
        return css`
            background: linear-gradient(
                90deg,
                rgb(from ${theme.palette[colorToken]} r g b / 0%) 0%,
                rgb(from ${theme.palette[colorToken]} r g b / 50%) 28%,
                rgb(from ${theme.palette[colorToken]} r g b / 100%) 45%
            );
        `;
    } else {
        return css`
            background: linear-gradient(
                90deg,
                rgb(from ${theme.palette[colorToken]} r g b / 0%) 50%,
                rgb(from ${theme.palette[colorToken]} r g b / 50%) 75%,
                rgb(from ${theme.palette[colorToken]} r g b / 100%) 90%
            );
        `;
    }
};
