import { AnyAction, ThunkDispatch } from '@reduxjs/toolkit';

import type OpenFin from '@openfin/core';

import { raiseBrowserPageAnalytics } from '@common/utils/usage-register';
import { currentOFIdentity, getCurrentOFWindow } from '@common/utils/window';

import { setLanguageInI18next } from '@common/api/i18next';
import { AttachedPageInternal } from '@common/api/pages/shapes';
import { BrowserChannelAction } from '@common/api/protocol/browser';

import { setCurrentScheme } from '@common/slices/target-theming';

import {
    attachPagesToWindow,
    detachPagesFromWindow,
    getActivePageForWindow,
    getPages,
    handleViewAttached,
    handleViewCrashed,
    isActivePageChanging,
    isDetachingPages,
    renamePage,
    reorderPagesForWindow,
    setActivePageForWindow,
    updatePageForWindow,
    updatePagesWindowOptions
} from '@enterprise/api/pages/attached';
import { registerDirty, registerViewDirtyEvents } from '@enterprise/api/pages/dirty';
import { makeLayoutManagerOverride } from '@enterprise/api/pages/layout-manager';
import { dispatch, setDispatchInstance, setGetStateInstance } from '@enterprise/api/pages/state';
import { AttachedPagesRootState, updatePageAtIndex } from '@enterprise/slices/pages/attached';

import { replaceLayout } from '@enterprise/utils/layout';

import {
    requestAddToChannel,
    requestCloseBrowserWindow,
    requestClosePage,
    requestDuplicatePage,
    requestOpenSaveModal,
    requestRemoveFromChannel,
    requestShowBrowserIndicator
} from '@enterprise/api/controls';
import { getLayoutsWithSelectedViewsInternal } from '@enterprise/api/layout';
import { handlePanelViewDetached, setUpPanelViews } from '@enterprise/api/panels';

import { TabFocusRootState } from '@enterprise/slices/tab-focus';

import { getCurrentSync } from '@client-platform/api';

export const registerBrowserProtocolActions = (channel: OpenFin.ChannelProvider) => {
    channel.register(BrowserChannelAction.CloseBrowserWindow, requestCloseBrowserWindow);
    channel.register(BrowserChannelAction.AddToChannel, requestAddToChannel);
    channel.register(BrowserChannelAction.RemoveFromChannel, requestRemoveFromChannel);
    channel.register(BrowserChannelAction.ClosePage, requestClosePage);
    channel.register(BrowserChannelAction.OpenSaveModalInternal, requestOpenSaveModal);
    channel.register(BrowserChannelAction.DuplicatePage, requestDuplicatePage);
    channel.register(BrowserChannelAction.ShowBrowserIndicator, requestShowBrowserIndicator);
};

async function setupActivePage(attachedPages: AttachedPageInternal[]): Promise<AttachedPageInternal> {
    // when we init a new window, the layout will be unlocked
    // therefore we need to update the layout with the locked version
    let activePageIndex = attachedPages.findIndex((page) => page.isActive);
    if (activePageIndex === -1) {
        activePageIndex = 0;
    }
    const activePage = attachedPages[activePageIndex];

    if (activePage?.isLocked) {
        const layoutManager = fin.Platform.Layout.wrapSync({
            ...fin.me.identity,
            layoutName: activePage.layoutContainerKey
        });
        // pick up the layout from window options because Container overwrites generated view names
        const layout = await layoutManager.getConfig();
        await replaceLayout(layout, activePage);
        dispatch(
            updatePageAtIndex({
                index: activePageIndex,
                page: { ...activePage, isLayoutCreated: true }
            })
        );
    }
    // We call the setActivePage override to enable platform developers to effect page-dependent changes (e.g. disable a button if page is locked)
    return activePage;
}

/**
 * Sets up the protocol actions for the browser window
 *
 * @param channel The channel to register the actions on
 * @param storeDispatch The dispatch function to use for the store
 * @param storeGetState The getState function to use for the store
 *
 * @remarks We need to be cautions here. All `channel.register` calls MUST be synchronous. This is because Core waits for channels to be created before processing any channel.dispatch requests. However, if we make/have any async/await calls after the channel has been created but BEFORE the channel actions have been registered, this may cause the calls to channel.dispatch to fail. As there is no action currently registered. To get around this, we can ensure that any channel.register calls after `fin.InterApplicationBus.Channel.create` are done before any `await`s.
 * @remarks `win.addListener('view-detached', cb)` SHOULD come after the layout is initialized. Failure to do so may result views not being removed from Golden Layouts when they are detached if the event listener throws an error.
 */
export async function registerPagesProtocol(
    channel: OpenFin.ChannelProvider,
    storeDispatch: ThunkDispatch<unknown, unknown, AnyAction>,
    storeGetState: () => AttachedPagesRootState & TabFocusRootState
) {
    const setupPromises: Promise<unknown>[] = [];

    const win = getCurrentOFWindow();
    const browser = getCurrentSync().Browser.wrapSync(currentOFIdentity);

    const disableMultiplePages = !!(fin as any).__internal_.initialOptions?.workspacePlatform?.disableMultiplePages;

    setDispatchInstance(storeDispatch);
    setGetStateInstance(storeGetState);

    channel.register(BrowserChannelAction.GetPages, getPages);
    channel.register(BrowserChannelAction.GetActivePageForWindow, getActivePageForWindow);
    channel.register(BrowserChannelAction.UpdatePageForWindow, updatePageForWindow);
    channel.register(BrowserChannelAction.UpdatePagesWindowOptions, updatePagesWindowOptions);
    channel.register(BrowserChannelAction.IsDetachingPages, isDetachingPages);
    channel.register(BrowserChannelAction.IsActivePageChanging, isActivePageChanging);
    channel.register(BrowserChannelAction.DetachPagesFromWindow, detachPagesFromWindow);
    channel.register(BrowserChannelAction.SetSelectedScheme, (payload) => storeDispatch(setCurrentScheme(payload)));
    channel.register(BrowserChannelAction.SetSelectedLanguage, setLanguageInI18next);
    channel.register(BrowserChannelAction.SetActivePageForWindow, setActivePageForWindow);
    channel.register(BrowserChannelAction.GetLayoutsWithSelectedViewsInternal, () =>
        getLayoutsWithSelectedViewsInternal(storeGetState)
    );

    setupPromises.push(win.addListener('view-detached', handlePanelViewDetached));
    setupPromises.push(win.addListener('view-crashed', handleViewCrashed));
    setupPromises.push(win.addListener('view-attached', handleViewAttached as any));
    setupPromises.push(win.addListener('close-requested', (e) => requestCloseBrowserWindow()));

    if (disableMultiplePages) {
        channel.setDefaultAction(() => {
            throw new Error(`This API cannot be called on this browser window as pages have been disabled.`);
        });
    } else {
        channel.register(BrowserChannelAction.AttachPagesToWindow, attachPagesToWindow);
        channel.register(BrowserChannelAction.RenamePage, renamePage);
        channel.register(BrowserChannelAction.ReorderPagesForWindow, reorderPagesForWindow);
    }

    /* Need to register view navigation events prior to view creation.
     * Do not merge with registerDirty(), tab events need to be registered after layout setup */
    await registerViewDirtyEvents();

    await win.once('layout-ready', async () => {
        const attachedPages = storeGetState().attachedPages.attachedPages;

        const activePage = await setupActivePage(attachedPages);

        setupPromises.push(browser.setActivePage(activePage.pageId));

        setupPromises.push(updatePagesWindowOptions());
        raiseBrowserPageAnalytics(attachedPages, 'Open Page');

        setupPromises.push(setUpPanelViews(attachedPages.find((page) => page.isActive).panels));

        if (!disableMultiplePages) {
            registerDirty();
        }

        await Promise.all(setupPromises);

        fin.InterApplicationBus.publish('browser-window-ready', { name: fin.me.name });
    });

    await fin.Platform.Layout.init({ layoutManagerOverride: makeLayoutManagerOverride(storeDispatch, storeGetState) });
}
